<template>
  <div class="game-container">
    <h1>Tic Tac Toe</h1>
    <div :class="['tic-tac-toe-board', { 'fade-out': gameOver }]">
      <div
        v-for="(cell, index) in board"
        :key="index"
        :class="['tic-tac-toe-cell', cellClass(cell, index)]"
        @click="makeMove(index)"
      >
        {{ cell }}
      </div>
      <div v-if="winningLine" :class="['winning-line', winningLineClass]"></div>
    </div>
    <div v-if="!gameOver" class="announcement">
      <p>It's <span :class="currentPlayerClass">{{ currentPlayer }}</span>'s turn!</p>
    </div>
  </div>
</template>

<script>
import { reactive, ref, computed } from 'vue';

export default {
  emits: ['gameEnd'],
  setup(_, { emit }) {
    const board = reactive(Array(9).fill(null));
    const currentPlayer = ref(Math.random() < 0.5 ? 'X' : 'O');
    const winningLine = ref(null);
    const gameOver = ref(false);

    const makeMove = (index) => {
      if (!board[index] && !winningLine.value) {
        board[index] = currentPlayer.value;
        const winCombination = checkWin();  
        if (winCombination) {
          winningLine.value = winCombination;
          setTimeout(() => {
              gameOver.value = true;
              setTimeout(() => {
                emit('gameEnd', currentPlayer.value);
              }, 600); 
          }, 1800);
        } else if (board.every(cell => cell !== null)) {
          gameOver.value = true;
          setTimeout(() => {
            emit('gameEnd');
          }, 600);
        } else {
          currentPlayer.value = currentPlayer.value === 'X' ? 'O' : 'X';
        }
      }
    };

    const cellClass = (cell, index) => {
        const classes = [];
        if (winningLine.value && winningLine.value.includes(index)) {
            classes.push('winning-cell');
        }
        if (cell === 'X') {
            classes.push('cross');
        } else if (cell === 'O') {
            classes.push('circle');
        }
        return classes;
    };

    const checkWin = () => {
      const winningCombinations = [
        [0, 1, 2], [3, 4, 5], [6, 7, 8], // Rows
        [0, 3, 6], [1, 4, 7], [2, 5, 8], // Columns
        [0, 4, 8], [2, 4, 6]             // Diagonals
      ];

      for (let combination of winningCombinations) {
        const [a, b, c] = combination;
        if (board[a] && board[a] === board[b] && board[a] === board[c]) {
          return combination;
        }
      }

      return null;
    };

    const currentPlayerClass = computed(() => {
      return currentPlayer.value === 'X' ? 'player-x' : 'player-o';
    });

    const winningLineClass = computed(() => {
        if (!winningLine.value) return '';

        const [a, b, c] = winningLine.value;
        
        // Handle horizontal lines
        if (a % 3 === 0 && b === a + 1 && c === a + 2) {
            const rowIndex = Math.floor(a / 3);
            return `horizontal-line row-${rowIndex}`;
        }
        
        // Handle vertical lines
        if (a < 3 && b === a + 3 && c === a + 6) {
            const colIndex = a % 3;
            return `vertical-line col-${colIndex}`;
        }

        // Handle diagonal lines
        if (a === 0 && c === 8) {
            return 'diagonal-line-right';
        } else if (a === 2 && c === 6) {
            return 'diagonal-line-left';
        }
    });

    return { board, makeMove, cellClass, winningLine, currentPlayer, currentPlayerClass, winningLineClass, gameOver };
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;700&display=swap');

