}

.announcement p {
    margin: 0;
}

.player-x {
    color: red;
}

.player-o {
    color: rgb(12, 229, 12);
}

.tic-tac-toe-board {
  position: relative;
  display: grid;
  background: white;
  grid-template-columns: repeat(3, 1fr);
