  background-color: rgb(55, 52, 52);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  font-weight: bold;
  cursor: pointer;
  color: white;
  transition: background-color 0.3s;
  font-family: 'Rubik', sans-serif;
}

.fade-out {
  animation: fadeOut 0.5s ease-in-out forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes drawHorizontalLine {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes drawVerticalLine {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}

@keyframes drawDiagonalLineRight {
  from {
    width: 0;
  }
  to {
    width: 141.4%; /* Adjust width to match the diagonal length */
  }
}

@keyframes drawDiagonalLineLeft {
  from {
    width: 0;
  }
  to {
    width: 141.4%; /* Adjust width to match the diagonal length */
  }
}

.winning-line {
  position: absolute;
  width: 2px;
  height: 5px;
  background-color: white;
  z-index: 10;
}

.horizontal-line {
  left: 0;
  width: 100%;
  height: 5px;
  position: absolute;
  animation: drawHorizontalLine 1.5s ease-in-out forwards;
}

.vertical-line {
  top: 0;
  width: 5px;
  height: 100%;
  position: absolute;
  animation: drawVerticalLine 1.5s ease-in-out forwards;
}

.diagonal-line-right {
  left: 0;
  height: 5px;
  transform: rotate(45deg);
  width: 100%;
  position: absolute;
  transform-origin: left center;
  animation: drawDiagonalLineRight 1.5s ease-in-out forwards;
}

.diagonal-line-left {
  right: 0;
  height: 5px;
  width: 100%;
  position: absolute;
  transform: rotate(-45deg);
  transform-origin: right center;
  animation: drawDiagonalLineLeft 1.5s ease-in-out forwards;
}

.row-0 {
  top: 16.66%; 
}

.row-1 {
  top: 50%; 
}

.row-2 {
  top: 83.33%; 
}

.col-0 {
  left: 16.66%; 
}

.col-1 {
  left: 50%; 
}

.col-2 {
  left: 83.33%;
}

.cross {
    color: red;
}

.circle {
    color: rgb(12, 229, 12);
}
</style>