<template>
  <div class="end-container">
    <!-- <h1>Game Over</h1> -->
    <div v-if="winner" class="win-displayer">
      <p :class="['main-text', winnerClass]">
        {{ winner }}
      </p>
      <p class="info-text">wins!</p>
    </div>
    <div v-else class="win-displayer">
      <p class="main-text">Tie!</p>
    </div>
    <button class="home-button" @click="goHome">Go Home</button>
  </div>
</template>

<script>
import { computed } from 'vue';


export default {
  props: ['winner'],
  emits: ['goBack'],
  setup(props, { emit }) {
    const goHome = () => {
      emit('goBack');
    };

    const winnerClass = computed(() => {
      return props.winner === 'X' ? 'winner-x' : 'winner-o';
    });

    return { goHome, winnerClass };
  }
}
</script>

