<template>
  <div v-if="gameStatus === 'home'" class="home-container">
    <h1>Tic Tac Toe</h1>
    <button class="start-button" @click="startGame">Start Game</button>
  </div>
  <Game v-if="gameStatus === 'game'" @gameEnd="handleGameEnd" />
  <End v-if="gameStatus === 'end'" @goBack="handleGoHome" :winner="winner"/>
</template>

<script>
import { ref } from 'vue'
import Game from '@/views/Game.vue'
import End from '@/views/End.vue'

export default {
  components: { Game, End },
  setup() {
    const gameStatus = ref('home');
    const winner = ref(null);

    const startGame = () => {
      gameStatus.value = 'game';
    };

    const handleGameEnd = (_winner) => {
      gameStatus.value = 'end';
      winner.value = _winner;
    }

    const handleGoHome = () => {
      gameStatus.value = 'home';
    }

    return { gameStatus, winner, startGame, handleGameEnd, handleGoHome };
  }
}
</script>

